<template>
    <div class="container">
        <HeadTop title="兑换">
            <template v-slot:rightText>
            </template>
        </HeadTop>
        <div class="upload">
            <div class="form">
                <div class="li flex-between">
                    <div class="label">卖出UVC</div>
                    <div class="input-wrap flex-middle flex-between">
                        <van-field :clearable="true" v-model.trim="amountInMax" placeholder="請輸入">
                        </van-field>
                    </div>
                </div>
                <div class="li flex-between">
                    <div class="label">买入USDT</div>
                    <div class="input-wrap flex-middle flex-between">
                        <van-field :clearable="true" v-model.trim="amountOut" placeholder="請輸入">
                        </van-field>
                    </div>
                </div>
                <div class="li flex-between">
                    <div class="label">接收的钱包地址</div>
                    <div class="input-wrap flex-middle flex-between">
                        <van-field :clearable="true" v-model.trim="address" placeholder="請輸入">
                        </van-field>
                    </div>
                </div>
            </div>
            <div class="btn-wrap">
                <van-button v-if="!Number(uvAllowance) || BigNumber(uvAllowance).lte(amountInMax)" class="btn"
                    :loading="btnLoading" size="large" :loading-text="$t('index.授權') + '...'" @click="approveUv">{{
                        $t('index.授權key', { key: 'UV' }) }}</van-button>
                <van-button v-else class="btn" :loading="btnLoading" size="large" :loading-text="$t('index.提交') + '...'"
                    @click="submit">{{
                        $t('index.提交') }}</van-button>
            </div>
            <van-dialog v-model:show="showDialog">
                <img class="img" src="@/assets/images/dialog-succes@2x.png" alt="">
                <div class="text">{{ $t('index.上幣成功') }}</div>
            </van-dialog>
        </div>
    </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import UvStakeAbi from "@/utils/UvStakeAbi";
import { Toast, Dialog } from 'vant';
import { MaxUint256 } from "@ethersproject/constants";
import BigNumber from 'bignumber.js';
import swapRouterAbi from "@/utils/swapRouterAbi";
export default {
    components: {
        HeadTop
    },
    setup () {
        const store = useStore()
        const { t } = useI18n()
        const data = reactive({
            $t: t,
            BigNumber: BigNumber,
            address: computed(() => store.state.accounts[0]),
            web3: computed(() => store.state.provider),
            balance: 0,
            showDialog: false,
            swapRouterContract: null,
            UsdtContract: null,
            UvContract: null,
            btnLoading: false,
            usdtAllowance: 0,
            uvAllowance: 0,
            amountOut: '',
            amountInMax: '',
            initContract: () => {
                data.swapRouterContract = new data.web3.eth.Contract(swapRouterAbi, config.swapRouter)
                data.UsdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
                data.UvContract = new data.web3.eth.Contract(tokenAbi, config.uvAddress)
                data.initData()
            },
            initData: () => {
                data.getUvAllowance()
            },
            // getUsdtAllowance: async () => {
            //     let result = await data.UsdtContract.methods.allowance(data.address, config.swapRouter).call();
            //     let decimalsUSDT = await data.UsdtContract.methods.decimals().call()
            //     result = new BigNumber(result).dividedBy(10 ** decimalsUSDT).toFixed()
            //     data.usdtAllowance = result;
            // },
            // approveUsdt: async () => {
            //     data.btnLoading = true
            //     try {
            //         await data.UsdtContract.methods.approve(config.swapRouter, MaxUint256).send({ from: data.address });
            //         data.getUsdtAllowance()
            //         data.btnLoading = false
            //     } catch (error) {
            //         data.btnLoading = false
            //     }
            // },
            getUvAllowance: async () => {
                let result = await data.UvContract.methods.allowance(data.address, config.swapRouter).call();
                let decimals = await data.UvContract.methods.decimals().call()
                result = new BigNumber(result).dividedBy(10 ** decimals).toFixed()
                console.log(result, 'getUvAllowance')
                data.uvAllowance = result;
            },
            approveUv: async () => {
                data.btnLoading = true
                try {
                    await data.UvContract.methods.approve(config.swapRouter, MaxUint256).send({ from: data.address });
                    data.getUvAllowance()
                    data.btnLoading = false
                } catch (error) {
                    data.btnLoading = false
                }
            },
            submit: async () => {
                try {
                    let amountOut = data.amountOut
                    let amountInMax = data.amountInMax

                    let decimalsUVC = await data.UvContract.methods.decimals().call()
                    let decimalsUSDT = await data.UsdtContract.methods.decimals().call()

                    amountOut = new BigNumber(amountOut).multipliedBy(10 ** decimalsUSDT).toFixed()
                    amountInMax = new BigNumber(amountInMax).multipliedBy(10 ** decimalsUVC).toFixed()
                    let path = [config.uvAddress, config.usdtAddress]
                    let to = data.address
                    let deadline = Math.floor((new Date).getTime() / 1000) + 1200
                    console.log({
                        amountOut, amountInMax, path, to, deadline
                    })
                    let estimateGas = await data.swapRouterContract.methods.swapTokensForExactTokens(amountOut, amountInMax, path, to, deadline).estimateGas({ from: data.address });
                    console.log('estimateGas', estimateGas)
                    await data.swapRouterContract.methods.swapTokensForExactTokens(amountOut, amountInMax, path, to, deadline).send({ from: data.address });
                } catch (error) {
                    function toRawType (value) {
                        return Object.prototype.toString.call(value).slice(8, -1);
                    }
                    const errorformat = (error, callback) => {
                        if (Object.prototype.hasOwnProperty.call(error, 'info') && error.info) {
                            return console.log(error);
                        }
                        console.log('toRawType:', toRawType(error), 'error:', error);
                        let errorStr = error;
                        if (toRawType(error) === 'Error') {
                            errorStr = String(error);
                        } else {
                            errorStr = JSON.stringify(error);
                        }

                        if (typeof callback === 'object') {
                            callback();
                        } else {
                            // debugger
                            Dialog({
                                message: errorStr
                            });
                        }
                    };
                    errorformat(error)
                }
            }
        })
        if (data.address) {
            data.initContract()
        }
        watch(() => data.address, (val) => {
            if (val) {
                data.initContract()
            }
        })
        const refData = toRefs(data);
        return {
            ...refData
        };
    }
}
</script>
